<template>
	<el-select
    ref="fontType"
		v-model="myType"
		placeholder="请选择字体"
    @change="handleChange"
	>
		<el-option
			v-for="item in fontStyleList"
			:key="item.enName"
			:label="item.name"
			:value="item.enName"
		>
		</el-option>
	</el-select>
</template>

<script>
	export default {
		name: "fontstyleList",
     model: {
       prop:"fontType",
      event: 'update'
    },
		props: {
			fontStyleList: {
				type: Array,
				default: () => {
					return [
						{
							name: "宋体",
							enName: "SimSun",
						},
						{
							name: "黑体",
							enName: "SimHei",
						},
					];
				},
			},
      // 父附件绑定的值
      fontType:{
         type: String,
        default: undefined
      }
		},
     created() {
      if (this.fontType && this.fontType.length > 0) {
        this.myType = this.fontType;
      }
    },
    data(){
      return{
        myType:undefined
      }
    },
    methods:{
     handleChange(val) {
        //触发update事件更新父组件绑定值
        this.$emit('update', val);
      }
    }
	};
</script>

