<template>
	<div id="app">
		<el-form
			:model="ruleForm"
			:rules="rules"
			ref="ruleForm"
			label-width="100px"
			class="demo-ruleForm"
		>
			<el-form-item
				label="标语名称"
				prop="sloganContent"
			>
				<el-input
					v-model="ruleForm.sloganContent"
					@keyup.enter.native="getSlogan($event)"
				></el-input>
			</el-form-item>
			<div
				id="box"
				:style="{backgroundColor:ruleForm.bgdColor,backgroundImage:'url('+bgdImgSrc+')'}"
			>
				<div
					id="content"
					class="content"
					v-drag
					:drag='xPosition'
					draggable="false"
				>
					<span
						ref="text"
						id="text"
						:style="{fontFamily:ruleForm.fontType,fontSize:ruleForm.fontSize,
        color:ruleForm.fontColor
        }"
					>{{contetText}}</span>
				</div>

			</div>
			<div id="middleForm">
				<el-row :gutter="20">
					<el-col :span="8">
						<el-form-item
							label="字体"
							prop="fontType"
						>
							<font-Type
								:font-style-list="fontStyleList"
								v-model="ruleForm.fontType"
							>

							</font-Type>
						</el-form-item>
					</el-col>
					<el-col :span="8">
						<el-form-item
							label="字号"
							prop="fontSize"
						>
							<el-select
								v-model="ruleForm.fontSize"
								placeholder="请选择字号"
							>
								<el-option
									v-for="item in fontSizeList"
									:key="item.value"
									:label="item.label"
									:value="item.value"
								>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>

					<el-col :span="8">
						<el-form-item
							label="背景颜色"
							prop="bgdColor"
						>
							<color-select
								:color-list="bgdColList"
								v-model="ruleForm.bgdColor"
							></color-select>
						</el-form-item>
					</el-col>
				</el-row>
				<el-row :gutter="20">
					<el-col :span="8">
						<el-form-item
							label="字体颜色"
							prop="fontColor"
						>
							<color-select
								:color-list="bgdColList"
								v-model="ruleForm.fontColor"
							></color-select>
						</el-form-item>
					</el-col>
					<el-col :span="8">
						<el-form-item label="背景图片">
							<input
								type="file"
								name="file"
								id="file"
								accept="image/*"
								@change='imgChange'
							/>
						</el-form-item>

					</el-col>
					<el-col :span="8"></el-col>
				</el-row>
			</div>
			<div id="endBtn">
				<el-row :span="20">
					<el-col :span="6">
						<el-form-item></el-form-item>
					</el-col>
					<el-col :span="9">
						<el-form-item>
							<el-button
								type="primary"
								@click="submitForm('ruleForm')"
							>保存</el-button>
							<el-button
								type="danger"
								@click="resetForm('ruleForm')"
							>重置</el-button>
						</el-form-item>
					</el-col>
					<el-col :span="8">
						<el-form-item></el-form-item>
					</el-col>
				</el-row>
			</div>

		</el-form>

	</div>
</template>

<script>
	// 引入公共组件
	import fontType from "../components/fontStyle.vue";
	import colorSelect from "../components/backgroundColor.vue";
	// 引入json
	import backgroundColor from "../../public/backgroud/backgroundColor.json";
	import fontStyle from "../../public/font/fontFamily.json";
	import fontSize from "../../public/font/fontSize.json";

	export default {
		name: "App",
		components: {
			colorSelect,
			fontType,
		},

		data() {
			return {
				// x轴坐标
				xPosition: null,
				yPosition: null,
				bgdImgSrc: null,
				// 颜色列表
				bgdColList: [],
				// 字体样式
				fontStyleList: [],
				// 字体大小
				fontSizeList: [],
				// 文本内容
				contetText: "",
				ruleForm: {
					// 背景颜色
					bgdColor: "#FFC0CB",
					// 选择字体颜色
					fontColor: "#800080",
					// 标语内容
					sloganContent: "",
					// 字号
					fontSize: "",
					// 字体类型
					fontType: "",
				},
				rules: {
					sloganContent: [
						{
							required: true,
							message: "请输入标语",
							trigger: "blur",
						},
					],
					fontType: [
						{
							required: true,
							message: "请选择字体",
							trigger: "change",
						},
					],
					fontSize: [
						{
							required: true,
							message: "请选择字号",
							trigger: "change",
						},
					],
					bgdColor: [
						{
							required: true,
							message: "请选择背景颜色",
							trigger: "change",
						},
					],
					fontColor: [
						{
							required: true,
							message: "请选择字体颜色",
							trigger: "change",
						},
					],
				},
			};
		},
		directives: {
			drag(el, al, cl) {
				// console.log(cl);
				let oDiv = el; //当前元素
				// let self = this; //上下文
				//禁止选择网页上的文字
				document.onselectstart = function () {
					return false;
				};
				oDiv.onmousedown = function (e) {
					//鼠标按下，计算当前元素距离可视区的距离
					let disX = e.clientX - oDiv.offsetLeft;
					let disY = e.clientY - oDiv.offsetTop;
					// 获取当前元素
					let bigBox = document.getElementById("box");
					let text = document.getElementById("text");

					let maxX = bigBox.offsetWidth - text.offsetWidth;
					let maxY = bigBox.offsetHeight - text.offsetHeight;
					document.onmousemove = function (e) {
						//通过事件委托，计算移动的距离
						let l = e.clientX - disX;
						let t = e.clientY - disY;

						//移动当前元素
						if (l <= 0) {
							l = 0;
						} else if (l > maxX) {
							l = maxX;
						}
						if (t <= 0) {
							t = 0;
						} else if (t > maxY) {
							t = maxY;
						}
						cl.context.xPosition = l;
						cl.context.yPosition = t;

						oDiv.style.left = l + "px";
						oDiv.style.top = t + "px";
					};
					document.onmouseup = function () {
						document.onmousemove = null;
						document.onmouseup = null;
					};
					//return false不加的话可能导致黏连，就是拖到一个地方时div粘在鼠标上不下来，相当于onmouseup失效
					return false;
				};
			},
		},
		created() {
			console.log(JSON.parse(localStorage.getItem("info")));
			let Info = JSON.parse(localStorage.getItem("info"));

			this.ruleForm.fontType = Info.typefaceInfo;
			this.ruleForm.fontSize = Info.fontSizeInfo;
			this.ruleForm.bgdColor = Info.backgroundColorInfo;
			this.ruleForm.fontColor = Info.fontColorInfo;
			(this.bgdImgSrc = Info.backgroundImgInfo),
				(this.ruleForm.sloganContent = Info.sloganInfo),
				(this.contetText = Info.sloganInfo),
				(this.xPosition = Info.xInfo),
				(this.yPosition = Info.yInfo);
			// console.log(JSON.parse(localStorage.getItem("position")));
		},
		mounted() {
			console.log(this.xPosition);
			console.log(document.getElementById("text").offsetLeft);
			document.getElementById("content").style.left = this.xPosition + "px";
			document.getElementById("content").style.top = this.yPosition + "px";
			// document
			// 	.getElementById("text")
			// 	.setAttribute("offsetLeft", this.xPosition+"px");
			// document
			// 	.getElementById("text").offsetTop
			// 	.setAttribute("offsetTop", this.yPosition);
			this.fontSizeList = fontSize;
			this.fontStyleList = fontStyle;
			this.bgdColList = backgroundColor;
		},
		methods: {
			imgChange(event) {
				let files = event.target.files || event.dataTransfer.files;
				var imgSrcI = this.getObjectURL(files[0]);
				console.log(imgSrcI);
				this.bgdImgSrc = imgSrcI;
			},
			getObjectURL(file) {
				var url = null;
				if (window.createObjectURL != undefined) {
					// basic
					url = window.createObjectURL(file);
				} else if (window.URL != undefined) {
					// mozilla(firefox)
					url = window.URL.createObjectURL(file);
				} else if (window.webkitURL != undefined) {
					// webkit or chrome
					url = window.webkitURL.createObjectURL(file);
				}
				return url;
			},
			getSlogan() {
				this.contetText = this.ruleForm.sloganContent;
			},
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						let baseInfo = {
							// 字体
							typefaceInfo: this.ruleForm.fontType,
							// 字号
							fontSizeInfo: this.ruleForm.fontSize,
							// 背景颜色
							backgroundColorInfo: this.ruleForm.bgdColor,
							// 字体颜色
							fontColorInfo: this.ruleForm.fontColor,
							//  背景图片
							backgroundImgInfo: this.bgdImgSrc,
							// 标语
							sloganInfo: this.ruleForm.sloganContent,
							// 坐标
							xInfo: this.xPosition,
							yInfo: this.yPosition,
						};
						localStorage.setItem("info", JSON.stringify(baseInfo));
						console.log("存在");
					} else {
						console.log("error submit!!");
						return false;
					}
				});
			},
			resetForm() {
				this.ruleForm={}
				this.ruleForm.bgdColor = "#FFC0CB";
				this.ruleForm.fontColor = "#800080";
				// this.$refs[formName].resetFields();
				this.contetText = "";
				this.bgdImgSrc = "";
				this.submitForm()
			},
		},
	};
</script>

<style lang="less" scoped>
	#app {
		border-radius: 10px;
		padding: 10px;
		border: 2px solid #ccc;
		width: 700px;

		#box {
			margin: 20px;
			height: 300px;
			background-repeat: no-repeat;
			background-size: 100% 100%;
			position: relative;
			.content {
				position: absolute;
				top: 0;
				left: 0;
				cursor: pointer;
			}
		}
		#middleForm,
		#endBtn {
			width: 680px;
		}
	}
</style>


